@import "variables";
@import "base/functions";
@import "base/mixins";
@import "woocommerce/mixins";
@import "woocommerce/variables";
@import "woocommerce/select2";
@import "woocommerce/checkoutbar";

/**
* Cart page
*/
table.cart {
    img {
        @apply h-auto;
    }

    td.actions {
        @apply text-right bg-transparent;

        .button {
            @screen lg{
                margin: 0 0 0 5px;
            }
        }

        button,
        input[type=submit] {
            @apply text-sm rounded-sm;
            padding-left: 22px;
            padding-right: 22px;
            color: #111;
        }

        .coupon {
            @apply float-left;

            label {
                @apply hidden;
            }
        }
    }
}


.cart-collaterals {
    @include clearfix();

    @screen lg{
        @apply float-right;
        width: 33%;
    }

    .related {
        @apply float-left;
        width: 30.75%;
    }

    .cross-sells {
        @apply float-left;
        width: 48%;

        ul.products {
            @apply float-none;

            li {
                width: 48%;
            }
        }
    }
}

.woocommerce-shipping-methods,
.woocommerce-shipping-destination{
    @apply mb-2;
}

.woocommerce-shipping-calculator {
    .button {
        @apply w-full;
    }

    .col2-set {
        .col-1,
        .col-2 {
            width: 47%;
        }
    }
}

.shipping-calculator-button{
    @apply underline flex items-center justify-end whitespace-nowrap mb-1;
    &::after {
        @include iconafter("");
    }
}

.woocommerce-cart-form {
    @screen lg{
        @apply float-left mb-0;
        width: 62%;
    }

    table {
        @screen lg{
            border-width: 0 0 1px;
        }

        img {
            @apply w-8 shadow-none;
        }

        .product-thumbnail {
            min-width: 32px;
            img{
                min-width: 64px;
            }
        }

        th {
            @apply text-gray-600 text-left;

            &.product-subtotal{
                @apply text-right;
            }
        }

        td {
            @apply border-b border-gray-300;

            @screen xl{
              @apply  py-4;
            }
        }

        th,td{
            @apply align-middle;
            @screen lg{
                @apply pl-4;
            }
        }

        td.actions {
            @media screen and (max-width: $tablet) {
                @apply border-b-0;
            }
            .coupon {
                @apply flex;

                .input-text {
                    @apply leading-normal outline-none text-sm border border-secondary;
                    margin: 0 -1px 0 0;

                    @screen lg{
                        width: 200px !important;
                    }
                }
            }
        }

        div.quantity {
            @apply m-0 float-none align-middle flex;
        }

        input {
            @apply m-0 align-middle;
        }

        small.stock {
            @apply block float-none;
        }
    }

    .variations {
        @apply mb-4 border-0 w-full;

        td,
        th {
            @apply border-0 align-top leading-loose;
        }

        label {
            @apply font-bold;
        }

        td.label {
            @apply pr-4;
        }
    }
}

td.product-name {
    dl.variation, .wc-item-meta {
        list-style: none outside;

        dt, .wc-item-meta-label {
            @apply float-left clear-both inline-block;
            margin-right: 0.25em;
            list-style: none outside;
        }

        dd {
            @apply m-0;
        }

        p,
        &:last-child {
            @apply mb-0;
        }
    }

    p.backorder_notification {
        font-size: 0.83em;
    }

    a {
        @apply text-gray-800;
    }
}

td.product-quantity {
    min-width: 80px;
}

.woocommerce {
    .wc-proceed-to-checkout .button {
        @include button-bg(#3bb54a, darken(#3bb54a, 5%), darken(#3bb54a, 8%));
        @apply text-white block border-0;
        height: 58px;
        line-height: 58px;
        font-size: 19px;
        padding: 0 1.2em;
        border-radius: 3px;
    }
}

.cart_totals {
    h2{
        @apply mb-2;
    }
    .shop_table {
        border: 1px solid #e2e2e2;
        border-bottom: none;
        table-layout: fixed;
        background-color: #f9f9f9;
        font-size: 15px;
        margin-bottom: 1rem;

        td, th {
            padding: 1em 22px;
            color: #111;
            border-top-width: 0;
            border-bottom: 1px solid #e2e2e2;
        }
    }
}


